.flotingdiv {
  display: flex;
  justify-content: space-around;
  background: white;
  box-shadow: var(--boxShadow);
  border-radius: 17px;
  align-items: center;
  padding: 0px 28px 0px 0px;
  height: 4rem;
}
.flotingdiv > img {
  transform: scale(0.4);
}
.flotingdiv > span {
  font-family: sans-serif;
  font-size: 16px;
  color: black;
}
